import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";
// import Banner from "src/components/Banner";

export interface DangsanProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;

            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
            letter-spacing: normal;
            color: #000000;
        }
        & > div:nth-child(2) {
            width: 80%;
            & > h1 {
                margin-bottom: 34px;
                font-size: 32px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #000000;
            }

            & > p {
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.71;
                letter-spacing: normal;
            }

            & > img {
                width: 100%;
                height: 430px;
                background-color: #aaa;
            }
        }
    }
`;

export default class Dangsan extends React.Component<DangsanProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "프로젝트", link: "/project/" }, { name: "당산", link: "/dangsan" }]}
                />
                <Content>
                    <Desc>
                        <div>
                            <div />
                            <div>
                                <img src={require("../../assets/project/dangsan.png")} alt="" style={{ width: "100%", height: "auto" }} />
                            </div>
                        </div>
                        <div>
                            <div>당산 동화빌딩</div>
                            <div>
                                <h1>타일벽의 로맨스, 동화빌딩</h1>
                                <p>
                                    동화빌딩은 서울시 영등포구 당산로 241번지에 있다.
                                    <br />
                                    <br />
                                    영등포구 당산역 일대는 1984년 완공된 빛 바랜 지하철 2호선 역사를 중심으로 서울 서부권 교통의 중 심으로
                                    발전해왔다. <br />
                                    역사 3번 출구 앞에 자리한 동화빌딩은 회벽색 시멘트의 당산역과 조화를 이루며 단단한 자세로 자리를 지키고
                                    있다.
                                    <br />
                                    <br />
                                    당산역 일대에는 1990년 문을 연 현대직업전문학교가 있다. 이 학교는 대한민국 청년들의 역량 개발과 직업
                                    능력 개발을 목적으로 <br />
                                    설립되었다. 본래 동화빌딩은 이 학교의 소유였다. 청년들이 내일을 꿈꾸던 이 건물은 2018년,
                                    유니언플레이스에 의해 재해석되었다.
                                    <br />
                                    <br />
                                    실력 있는 목수를 꿈꾸던 학생들의 실습실로 활용되어 온 지하는 로컬피플의 건강을 얻는 공간으로, 외국인
                                    노동자들이 <br />
                                    희망을 배우던 2층과 3층은 청년들의 외국어 배움과 외식 창업을 준비하는 공간으로 각각 바뀌었다. <br />
                                    한강이 시원스레 내려다 보이고, 지하철이 내 품 안으로 들어오는 듯한 건물의 상층부 는 글로벌 청년들을 위한
                                    주거공간이 되었다.
                                    <br />
                                    <br />
                                    변하지 않는 것은 유행을 타지 않는다. 옛 건축물의 아름다움은 남기고 현대의 모던함을 간결하게 더한
                                    동화빌딩은 <br />
                                    과거와 현대의 아름다운 조화로움 속에 젊은 당산 그 맨 앞에 서 있다.
                                    <br />
                                </p>
                                <iframe
                                    width="100%"
                                    height="430px"
                                    src="https://www.youtube.com/embed/04IngVJuZk8"
                                    style={{ marginTop: 10, marginBottom: 10 }}
                                />
                                <p>
                                    사람들이 모여 사는 마을에는 집과 학교, 작은 카페나 식당처럼 다양한 공간들이 있습니다. <br />
                                    우리는 도시 사람들을 하나로 연결하고, 살고 싶은 집, 다니고 싶은 학교, <br />
                                    맛있는 나만의 카페와 식당들을 만들고 싶었습니다. <br />
                                    <br />
                                    하지만 땅값이 비싼 도심에서는 이러한 생각이 실현되기 어려웠습니다. <br />
                                    그래서 우리는 마을을 가로가 아닌 세로로 기획하기 시작했습니다. <br />
                                    <br />
                                    만약 주거 공간과 교육 공간, 커뮤니티 공간, 식당, 빵집, 피트니스 등 <br />
                                    우리 삶에 필요한 다양한 공간들이 한 건물에 있다면 얼마나 좋을까요? <br />
                                    <br />
                                    그래서 우리는 비교적 저렴한 도심 속 낡은 공간을 찾았고, <br />
                                    지금의 당산동 동화빌딩을 첫번째 유니언타운 부지로 낙점했습니다. <br />
                                    우리는 외국인 노동자와 직업훈련생들의 교육 공간이었던 28년 된 이 건물을 <br />
                                    새로운 도시 마을로 바꾸기 시작했습니다. <br />
                                    <br />
                                    그리고 지금 유니언타운의 지하에는 활력이 넘치는 피트니스가, <br />
                                    1층에는 베이커리 카페, 2층에는 잉글리시 카페, 3층에는 공유 키친, <br />
                                    4층부터 6층까지는 코-워킹 오피스 그리고 7층과 8층에는 쉐어하우스가 탄생했습니다. <br />
                                    <br />
                                    비어 있던 공간에서 <br />
                                    함께 공유하고 싶은 공간으로의 재탄생. <br />
                                    <br />
                                    사람들이 함께 살고 머무르며, 같이 일 하고 즐기는 곳. <br />
                                    여기는 세상에 없던 도심 속 새로운 마을, ‘유니언타운’입니다.
                                </p>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



